/*

=====================
SIDEBAR + RIGHT COLUMN
=====================

*/
#sidebar {
    padding-top: 40px;
    position: fixed;
}
@media screen and (min-width: 992px) {
.sidebar-content {
        padding-left: 20px;
        padding-right: 20px;
}
}
.sidebar-heading {
    font-size: 18px;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    text-align: left;
    color: #555555;
}
.sidebar-heading a {
    color: #000;
    text-decoration: none;
    -webkit-transition: all 0.2s ease-out;
    -moz-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
}
.sidebar-heading a:hover {
    border-bottom: dotted 1px #ef5285;
    color: #000;
    text-decoration: none;
}
.sidebar-p {
    color: #999999;
    font-size: 14px;
}
.sidebar-menu {
    list-style-type: none;
    padding-left: 20px;
    margin-bottom: 40px;
}
.sidebar-menu li {
    margin-bottom: 5px;
}
.sidebar-menu li a {
    color: #555555;
}
.sidebar-menu li:before {
    font-family: 'FontAwesome';
    content: '\f105';
    margin: 0 5px 0 -15px;
    color: #999999;
}
.sidebar-menu li a.router-link-exact-active {
    color: #ef5285;
    font-weight: bold;
}
.sidebar-menu li.active:before {
    color: #ef5285;
}
.sidebar-menu li:hover a {
    text-decoration: none;
    color: #ef5285;
}
.sidebar-menu li:hover:before {
    color: #ef5285;
}
.content-column {
    padding-top: 40px;
    margin-left: 25%;
}
.content-column.white-background {
    background: #fff;
    min-height: 100vh;
}
.grid {
    padding-top: 5px;
    overflow-y: hidden;
}
.content-column-content {
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 20px;
}
@media (min-width: 1200px) {
.content-column-content {
        padding-left: 40px;
        padding-right: 40px;
}
}
.content-column-content p.lead {
    color: #555555;
}
.content-column-content p,
.content-column-content li {
    color: #999999;
}
.content-column-content ul li {
    list-style-type: none;
}
.content-column-content ul li:before {
    font-family: 'FontAwesome';
    content: '\f105';
    margin: 0 5px 0 -15px;
    color: #ef5285;
}
.small-navbar {
    margin-bottom: 20px;
}
.small-navbar:before,
.small-navbar:after {
    content: " ";
    display: table;
}
.small-navbar:after {
    clear: both;
}
.small-navbar-heading {
    float: right;
    font-size: 18px;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    margin-top: 10px;
}
.small-navbar-heading a {
    color: #000;
    text-decoration: none;
    -webkit-transition: all 0.2s ease-out;
    -moz-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
}
.small-navbar-heading a:hover {
    border-bottom: dotted 1px #ef5285;
    color: #000;
    text-decoration: none;
}


/*

=====================
OFF-CANVAS SIDEBAR BEHAVIOUR
=====================

*/
@media screen and (max-width: 767px) {
.row-offcanvas {
        position: relative;
        -webkit-transition: all 0.25s ease-out;
        -o-transition: all 0.25s ease-out;
        transition: all 0.25s ease-out;
        opacity: 1;
}
.row-offcanvas-right {
        right: 0;
}
.row-offcanvas-left {
        left: 0;
}
.row-offcanvas-right .sidebar-offcanvas {
        right: -50%;
        /* 6 columns */
}
.row-offcanvas-left .sidebar-offcanvas {
        left: -48%;
        /* 6 columns */
}
.row-offcanvas-right.active {
        right: 50%;
        /* 6 columns */
}
.row-offcanvas-left.active {
        left: 50%;
        /* 6 columns */
}
.sidebar-offcanvas {
        position: absolute;
        top: 0;
        width: 50%;
        /* 6 columns */
}
}


