/* scaffolding */
body {
    font-family: "Roboto", Helvetica, Arial, sans-serif;
    font-size: 16px;
    line-height: 1.55;
    color: #000000;
    background-color: #f4f4f4;
}
a {
    color: #ef5285;
    text-decoration: none;
}
a:hover,
a:focus {
    color: #e01557;
    text-decoration: underline;
}
a:focus {
    outline: thin dotted;
    outline: 5px auto -webkit-focus-ring-color;
    outline-offset: -2px;
}
.img-rounded {
    border-radius: 0;
}
hr {
    margin-top: 24px;
    margin-bottom: 24px;
    border: 0;
    border-top: 1px solid #cdcdcd;
}
.progress {
    overflow: hidden;
    height: 24px;
    margin-bottom: 24px;
    background-color: #f5f5f5;
    border-radius: 0;
    -webkit-box-shadow: inset 0 0 0 rgba(0, 0, 0, 0);
    box-shadow: inset 0 0 0 rgba(0, 0, 0, 0);
}
.progress-bar {
    float: left;
    width: 0%;
    height: 100%;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    -webkit-box-shadow: inset 0 0 0 rgba(0, 0, 0, 0);
    box-shadow: inset 0 0 0 rgba(0, 0, 0, 0);
    -webkit-transition: width 0.6s ease;
    transition: width 0.6s ease;
}


/* breadcrumbs */
.breadcrumb {
    padding: 8px 0;
    margin-bottom: 24px;
    background-color: transparent;
    border-radius: 0;
    text-align: center;
}
.breadcrumb > li + li:before {
    content: "/\00a0";
    color: #cccccc;
}
.breadcrumb > .active {
    color: #999999;
}
.breadcrumb a {
    color: #ef5285;
}
@media (max-width: 991px) {
.breadcrumb {
        padding: 8px 0;
        text-align: center;
}
}


/* buttons  */
.btn {
    font-weight: 400;
    font-family: "Roboto", Helvetica, Arial, sans-serif;
    padding: 6px 12px;
    font-size: 16px;
    line-height: 1.55;
    border-radius: 0;
}
.btn-primary {
    color: #ffffff;
    background-color: #ef5285;
    border-color: #ef5285;
}
.btn-primary:hover,
.btn-primary:focus,
.btn-primary:active,
.btn-primary.active,
.open .dropdown-toggle.btn-primary {
    color: #ffffff;
