
html {
    font-family: Tahoma;
    font-size: 14px;
}
body {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 14px;
    line-height: 1.42857143;
    color: #333;
}
pre {
    overflow: auto;
}
pre .string {
    color: #885800;
}
pre .number {
    color: blue;
}
pre .boolean {
    color: magenta;
}
pre .null {
    color: red;
}
pre .key {
    color: green;
}
h1 {
    color: #555555;
    text-align: center;
    font-size: 36px;
    margin-top: 20px;
    margin-bottom: 10px;
    font-weight: 500;
    color: #555555;
}
span{
    color: #f06485;
}
span{
    color: #f06485;
}
fieldset {
    border: 0;
}
form {
    left: 25%;
}
.panel {
    margin-bottom: 20px;
    background-color: #fff;
    border: 1px solid transparent;
    border-radius: 4px;
    -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, .05);
    box-shadow: 0 1px 1px rgba(0, 0, 0, .05);
    border-color: #ddd;
}
.panel-heading {
    color: #333;
    background-color: #f5f5f5;
    border-color: #ddd;
    padding: 10px 15px;
    border-bottom: 1px solid transparent;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
}
.panel-body {
    padding: 15px;
    width: 50%;
    margin-left: 25%;
    margin-bottom: 40px;
}
.field-checklist .wrapper {
    width: 100%;
}


/*
*===============
* BUTTON
*===============
*/
button{
    border: none;
    color: #f06485;
    background-color: #f4f4f4;
    font-size: 16px;
    margin-left: 35%;
}




